import React, { useEffect, useContext, useState } from 'react';
import styled from 'styled-components'
import { mainStore, MainStateProvider } from '../store/MainStore'
import MenuIcon from '../components/MenuIcon';
import Eyes from '../components/Eyes';
import styles from '../constants'
const Container = styled.div`
    width: 100%;
    scroll-snap-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
`

const Content = styled.div`
    position: relative;
    width: 100%;
    height: 100%;
    background: ${styles.pink};
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: white;
`

const TimerContainer = styled.div`
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: white;
`

const TimerComponent = styled.div`
    height: 100%;
    margin-left: 50px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
`

const TimerNumber = styled.div`
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 80px;
`

const TimerText = styled.div`
    height: 50px;
    font-size: 25px;
    font-weight: bold;
`


interface propTypes {
    mouseY: any
    mouseX: any
    height: number
    width: number
    scroll: number
}

const date = new Date('25 August 2021').getTime()

function FirstPage(props: propTypes) {
    const {state, dispatch} = useContext(mainStore)

    const [currSec, setCurrSec] = useState('0')
    const [currMin, setCurrMin] = useState('0')
    const [currHours, setCurrHours] = useState('0')
    const [currDay, setCurrDay] = useState('0')

    useEffect(() => {
        let x = setInterval(function() {
            let now = new Date().getTime()
            let distance = date - now;

            // Time calculations for days, hours, minutes and seconds
            let days:any = Math.floor(distance / (1000 * 60 * 60 * 24));
            let hours:any = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
            let minutes:any = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
            let seconds:any = Math.floor((distance % (1000 * 60)) / 1000);

            setCurrSec(seconds)
            setCurrMin(minutes)
            setCurrHours(hours)
            setCurrDay(days)

        }, 1000)
    }, [])

    return (
        <Container
            style={{
                marginTop: state.mobileOpen ? `${state.mobilePadding}px` : `${state.fullPadding}px`,
                height: state.mobileOpen ? `${state.height - (state.mobilePadding * 2)}px` : `${state.height - (state.fullPadding * 2 )}px`,
            }}
        >


            <Content
                style={{
                    fontSize: state.mobileOpen ? '40px' : '70px',
                }}
            >
                <div style={{marginBottom: '50px', fontSize: '30px', fontWeight: 'normal'}}>GRADEX 2021:</div>



                <TimerContainer>
                    <TimerComponent
                        style={{marginLeft: '0px'}}
                    >
                        <TimerNumber>
                            {
                                currDay
                            }
                        </TimerNumber>
                        
                        <TimerText>
                            DAYS
                        </TimerText>
                    </TimerComponent>

                    <TimerComponent>
                        <TimerNumber>
                            {
                                currHours
                            }
                        </TimerNumber>
                        <TimerText>
                            HOURS
                        </TimerText>
                    </TimerComponent>

                    <TimerComponent>
                        <TimerNumber>
                            {
                                currMin
                            }
                        </TimerNumber>
                        <TimerText>
                            MINUTES
                        </TimerText>
                    </TimerComponent>

                    <TimerComponent>
                        <TimerNumber>
                            {
                                currSec
                            }
                        </TimerNumber>
                        <TimerText>
                            SECONDS
                        </TimerText>
                    </TimerComponent>
                </TimerContainer>

                <div
                    style={{
                        position: 'absolute',
                        bottom: '0px'
                    }}
                >
                    <Eyes 
                        mouseX={props.mouseX}
                        mouseY={props.mouseY}
                        height={props.height}
                        width={props.width}
                        scroll={props.scroll}
                        browser={state.browser}
                        mobileOpen={state.mobileOpen}
                    />
                </div>

                
                {/*
                <div>STELLENBOSCH</div>
                <div>GRADEX '21</div>
                */}
            </Content>

        </Container>
    );
}

export default FirstPage;