import React, { useEffect, useRef, useState, useContext } from 'react';
import styled from 'styled-components'
import FirstPage from './FirstPage'
import NavBar from '../components/NavBar'
import { mainStore, MainStateProvider } from '../store/MainStore'
import styles from '../constants'

import AboutPage from './AboutPage'
import GalleryPage from './GalleryPage'
import MeetUsPage from './MeetUsPage'
import EventsPage from './EventsPage';
import ContactPage from './ContactPage';
import TestPage from './TestPage'

import MenuIcon from '../components/MenuIcon';
import TransitionContainer from '../components/TransitionContainer';

const Container = styled.div`
    top: 0px;
    left: 0px;
    position: absolutew;
    height: 100%;
    width: 100%;
    background: ${styles.background};
    overflow-x: hidden;
    overflow-y: auto;
    scroll-behavior: smooth;
`

const ContentContainer = styled.div`
    flex-grow: 1;
    flex-shrink: 0;
    animation: all 0.3s ease;
    background: white;
`

const PaddingElements = styled.div`
    position: fixed;
    width: 100%;
    z-index: 10;
    left: 0px;
    background: ${styles.background};
    transition: all 0.3s ease;
`

const VerticalPaddingElements = styled.div`
    position: fixed;
    height: 100%;
    z-index: 10;
    background: ${styles.background};
    transition: all 0.3s ease;
`

const MenuContainer = styled.div`
    height: 100%;
    width: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 9;
    background: ${styles.black};
    top: 0px;
    left: 0px;
`

const MenuTopBottomContainer = styled.div`
    width: 100%;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
`

const MenuItemsContainer = styled.div`
    flex-grow: 1;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
`

const MenuItem = styled.div`
    color: white;
    height: calc(100% / 5);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    font-weight: bold;
    transition: all 0.2s ease;
    cursor: pointer;
    opacity: 0.8;
    &:hover {
        opacity: 1;
    }
`

const MenuIconContainer = styled.div`
    cursor: pointer;

    position: fixed;
    z-index: 20;
    display: flex;

    justify-content: center;
    align-items: center;
`

//    scroll-snap-type: y mandatory;

const height  = window.innerHeight
const width  = window.innerWidth

interface propTypes {

}

function MainPage(props: propTypes) {
    const {state, dispatch} = useContext(mainStore)
    const [mouseX, setMouseX] = useState(0)
    const [mouseY, setMouseY] = useState(0)
    const [pageScroll, setPageScroll] = useState(0)

    let padding = 90

    if(state.mobileOpen) 
        padding = 60
    const [menuTop, setMenuTop] = useState(height - padding)

    const scrollRef = useRef<HTMLDivElement>(null)
    const [animatingMenu, setAnimatingMenu] = useState(false)

    let timer:any = null


    useEffect(() => {

        document.addEventListener('mousemove', function(e: any){
            let newX = (e.clientX/width) * 100
            let newY = (e.clientY/height) * 100

            if(Math.abs(mouseX - newX) > 1 )    {
                setMouseX( newX)
            }
            if(Math.abs(mouseY - newY) > 1 )    {
                setMouseY( newY)
            }
        }, false)

        return () => {
            clearTimeout(timer)
        }
    
    }, [])

    return (
        <Container
            ref={scrollRef}
            id="container"
            onScroll={scrollEvent}
        >

            <PaddingElements 
                style={{
                    height: state.mobileOpen ? menuTop === 0 ? `${state.fullPadding}px` : `${state.mobilePadding}px` : `${state.fullPadding}px`,
                    top: '0px',
                }}
            />
            <PaddingElements 
                style={{
                    height: state.mobileOpen ? `${state.mobilePadding}px` : `${state.fullPadding}px`,
                    bottom: '0px'
                }}
            />

            <VerticalPaddingElements
                style={{
                    width: state.mobileOpen ? `${state.mobilePadding}px` : `${state.fullPadding}px`,
                    left: '0px'
                }}
            />

            <VerticalPaddingElements
                style={{
                    width: state.mobileOpen ? `${state.mobilePadding}px` : `${state.fullPadding}px`,
                    right: '0px'
                }}
            />
            <ContentContainer 
                style={{
                    width: state.mobileOpen ? `calc(100% - ${state.mobilePadding * 2}px)` : `calc(100% - ${state.fullPadding * 2}}px)`,
                    margin: state.mobileOpen ? `0px ${state.mobilePadding}px 0px ${state.mobilePadding}px` : `0px ${state.fullPadding}px 0px ${state.fullPadding}px`,
                }}
            >
                <FirstPage 
                    mouseX={mouseX}
                    mouseY={mouseY}
                    height={height}
                    width={width}
                    scroll={pageScroll}
                />

                <AboutPage 
                    open={pageScroll > (height * 0.35)}
                
                />
                <MeetUsPage 
                    open={pageScroll > ( (1 * height) + (height * 0.35) )}

                />
                <GalleryPage />
                <EventsPage />
                <ContactPage />


            </ContentContainer>

            <MenuIconContainer
                onClick={() => menuSetter(!state.menuOpen)}
                style={{
                    height: `${state.fullPadding}px`,
                    width: `${state.fullPadding}px`,

                    right: state.mobileOpen ? `${state.mobilePadding + 10}px` : `${state.fullPadding + 10}px`,
                    top: `${menuTop}px`,
                }}

            >
                <MenuIcon open={state.menuOpen} color={menuTop === 0 ? 'darkgrey' : 'white'} />
            </MenuIconContainer>

            <TransitionContainer
                zIndex={9}
                open={state.menuOpen}
                length={0.5}
                position={'absolute'}
                height={'100%'}
                width={'100%'}
                left={'0px'}
            >
                <MenuContainer>
                    <MenuTopBottomContainer
                        style={{
                            fontSize: '40px',
                            fontWeight: 'bold'
                        }}
                    >
                        GradeX
                    </MenuTopBottomContainer>

                    <MenuItemsContainer
                        style={{
                            flexDirection: state.mobileOpen ? 'column' : 'row',
                        }}
                    >
                        <MenuItem 
                            onClick={() => goTo('about')} 
                            style={{
                                transitionDelay: '0ms', 
                                marginLeft: state.mobileOpen ? animatingMenu ? '0px' : '200px' : '0px', 
                                marginTop: !state.mobileOpen ? animatingMenu ? '0px' : '200px' : '0px',
                                opacity: animatingMenu ? 0.8 : 0,
                                minWidth: state.mobileOpen ? '80%' : '100px'
                            }}
                        >
                            ABOUT US
                        </MenuItem>

                        <MenuItem 
                            onClick={() => goTo('meet')} 
                            style={{
                                transitionDelay: '100ms', 
                                marginLeft: state.mobileOpen ? animatingMenu ? '0px' : '200px' : '0px', 
                                marginTop: !state.mobileOpen ? animatingMenu ? '0px' : '200px' : '0px',
                                opacity: animatingMenu ? 0.8 : 0,
                                minWidth: state.mobileOpen ? '80%' : '100px'
                            }}
                        >
                            MEET US
                        </MenuItem>

                        <MenuItem 
                            onClick={() => goTo('gallery')} 
                            style={{
                                transitionDelay: '200ms', 
                                marginLeft: state.mobileOpen ? animatingMenu ? '0px' : '200px' : '0px', 
                                marginTop: !state.mobileOpen ? animatingMenu ? '0px' : '200px' : '0px',
                                opacity: animatingMenu ? 0.8 : 0,
                                minWidth: state.mobileOpen ? '80%' : '100px'
                            }}
                        >
                            GALLERY
                        </MenuItem>

                        <MenuItem 
                            onClick={() => goTo('events')} 
                            style={{
                                transitionDelay: '300ms', 
                                marginLeft: state.mobileOpen ? animatingMenu ? '0px' : '200px' : '0px', 
                                marginTop: !state.mobileOpen ? animatingMenu ? '0px' : '200px' : '0px', 
                                opacity: animatingMenu ? 0.8 : 0,
                                minWidth: state.mobileOpen ? '80%' : '100px'
                            }}
                        >
                            EVENTS
                        </MenuItem>

                        <MenuItem 
                            onClick={() => goTo('contact')} 
                            style={{
                                transitionDelay: '400ms', 
                                marginLeft: state.mobileOpen ? animatingMenu ? '0px' : '200px' : '0px', 
                                marginTop: !state.mobileOpen ? animatingMenu ? '0px' : '200px' : '0px', 
                                opacity: animatingMenu ? 0.8 : 0,
                                minWidth: state.mobileOpen ? '80%' : '100px'
                            }}
                        >
                            CONTACT
                        </MenuItem>

                    </MenuItemsContainer>
                    <MenuTopBottomContainer 
                        style={{
                            
                        }}
                    >
                    </MenuTopBottomContainer>

                </MenuContainer>
            </TransitionContainer>
            
        </Container>
    );

    function scrollEvent(e: any) {

        let scrollOff = e.target.scrollTop
        setPageScroll(scrollOff)

        let padding = 90

        if(state.mobileOpen) 
            padding = 60

        let offset = state.height - padding - scrollOff
        if(offset > 0) {

            setMenuTop(offset)
            
        }
        else {
            setMenuTop(0)
        }
        
    }

    function goTo(page: string) {
        let multiplier = 0;

        switch(page) {
            case "about":
                multiplier = 1
                break
            case "meet":
                multiplier = 2
                break
            case "gallery":
                multiplier = 3
                break
            case "events":
                multiplier = 4
                break
            case "contact":
                multiplier = 5
                break
        }

        setScroll(multiplier * height)
        menuSetter(false)
    }

    function menuSetter(val: boolean) {
        setMenuOpen(val)
        timer = setTimeout(() => setAnimatingMenu(val), 4)
    }

    function setScroll(top: number) {
        let scroller = document.getElementById("container")
        if(scroller) 
            scroller.scrollTop = top
    }

    function setMenuOpen(val: boolean) {
        dispatch({
            type: 'setMenu',
            menuOpen: val
        })
    }
}

export default MainPage;